#career {
  margin-left: auto;
  /* 이미지를 가운데 정렬하기 위해 추가 */
}

.image-container {
  display: flex;
  justify-content: center;
  /* 이미지를 수평으로 가운데 정렬하기 위해 추가 */
}

.career-image {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  margin: 20px;
}

fieldset {
  width: 100%;
  margin: 20px auto;
  font-size: 1.5em;
}

.bottom-fieldset {
  margin-bottom: 20px;
  /* 필요에 따라 조절 가능한 여백 추가 */
}