@import url('https://fonts.googleapis.com/css2?family=Bagel+Fat+One&family=Gothic+A1:wght@200;300&family=Nanum+Gothic+Coding:wght@700&display=swap');

em {
  color: rgb(243, 119, 167);
  font-family: 'Bagel Fat One', 'Nanum Gothic Coding', sans-serif;
  margin-left: 100px;
  margin-top: 100px;
  margin-bottom: 20px;
  /* 변경된 부분 */
  font-weight: bolder;
  font-size: 2em;
}

td img {
  width: 90%;
  height: auto;
}

table {
  font-family: 'Gothic A1', 'Nanum Gothic Coding', sans-serif;
  width: 80%;
  margin-left: 100px;
  border: 1px solid black;
  border-collapse: collapse;
  text-align: center;
  font-size: 1.5em;
  margin-top: 30px;
  margin-bottom: 80px;

}

td,
th {
  font-family: 'Gothic A1', 'Nanum Gothic Coding', sans-serif;
  width: 20%;
  border: 1px solid black;
  padding: 3px;
}

tr {
  font-family: 'Gothic A1', 'Nanum Gothic Coding', sans-serif;
  height: 30px;
}

#pledge {
  text-align: right;
  width: 50%;
  font-family: 'Gothic A1', 'Nanum Gothic Coding', sans-serif;
  margin-left: 950px;
  font-size: 1.5em;
}