@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700;800&display=swap');
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Bagel+Fat+One&family=Nanum+Gothic:wght@700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bagel+Fat+One&family=Nanum+Gothic:wght@700;800&display=swap');

body {
  font-family: 'Nanum Gothic', sans-serif;
  background-color: white;
  line-height: 1.5;
  font-weight: bolder;
  margin-left: 15px;
}

header {
  font-family: 'Bagel Fat One', 'Nanum Gothic', sans-serif;
  text-align: center;
  font-size: 40px;
  margin-bottom: 5px;
}

header .maintitle {
  margin-top: 8px;
  margin-bottom: 0px;
}

header .subtitle {
  color: rgb(221, 137, 151);
  margin-top: 0px;
  margin-bottom: 40px;
}

#index {
  font-size: 24px;
  margin-left: 20px;
}

.container {
  display: grid;
  grid-template-columns: 2fr;
}

nav {
  font-family: 'Nanum Gothic', sans-serif;
  margin-left:5px;
  margin-right:5px;
  font-size: 10px;
  background-color: pink;
  text-align: center;
}

nav a {
  display: inline-block;
  width: calc(25% - 10px);
  margin: 10px;
  font-size: 40px;
}

main {
  font-family: 'Nanum Gothic', sans-serif;
  margin-left: 20px;
  background-color: white;
  font-size: 18px;
}

footer {
  font-family: 'Nanum Gothic', sans-serif;
  text-align: center;
  background-color: gray;
  color: white;
  font-size: 15px;
}

a:link,
nav a:visited {
  font-family: 'Nanum Gothic', sans-serif;
  background-color: #000000;
  padding: 5px 10px;
  text-decoration: none;
  display: inline-block;
  width: 90px;
  font-size: 15px;
  color: pink;
}

a:hover,
a:active {
  background-color: #ea8080;
  color: white;
}

button {
  font-family: 'Nanum Gothic', sans-serif;
  background-color: rgb(243, 119, 167);
  color: white;
  padding: 10px 10px;
  margin: 10px;
  font-size: 14px;
  /* 수정된 글꼴 크기 */
  cursor: pointer;
  transition-duration: 1s;
  border: none;
  /* 테두리 없애기 */
  outline: none;
}

button:hover {
  background-color: black;
  color: white;
}
fieldset {
  width: 100%;
  box-sizing: border-box;
  /* 배경색이나 테두리까지 너비에 포함되도록 설정 */
  margin: 0 auto;
  font-size: 1em;
  padding: 10px;
  /* 선택적으로 패딩을 추가할 수 있습니다. */
}